@keyframes withdrawal {
    0% {
        color: #93388f;
        text-shadow: 0 0 6px #3b1443;
    }
    25% {
        color: #0098dc;
        text-shadow: 0 0 6px #00396d;
    }
    50% {
        color: #93388f;
        text-shadow: 0 0 6px #3b1443;
    }
    100% {
        color: #0098dc;
        text-shadow: 0 0 6px #00396d;
    }
}

@keyframes progressRed {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}

@keyframes textWarn {
    0% {
        color: #ffffff;
    }

    100% {
        color: #f2d2ed;
    }
}


.item-preview {
    display: flex;
    justify-content: center;
}

.item-details {
    width: 100%;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: stretch;
    font-family: "Press Start 2P";
}

.item-tag {
    padding: 0px 16px;
}

.text-color-withdrawal {
    font-weight: bold;
    animation-name: withdrawal;
    animation-duration: 8s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
    animation-timing-function: ease-in-out;
}

.card-button-remove {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    padding: 0 0;
    width: 100%;
}

.card-button-remove-name {
    z-index: 2;
}

.card-button-progress-bar-red {
    animation-name: progressRed;
    animation-duration: 2s;
    animation-fill-mode: forwards;

    position: absolute;
    background-color: #891e2b;
    min-height: 100%;
    justify-self: flex-start;
    left: 0;
    z-index: 1;
}

.card-button-name-warn {
    animation-name: textWarn;
    animation-duration: 2s;
    animation-fill-mode: forwards;
}

.item-description-notes {
    width: 100%;
    resize: none;
}